import { HashRouter, Route, Routes } from 'react-router-dom';

// Components
import { Login, AdminSpace, CreatePost, PostList } from './admin-space';
import { LandingPage, BlogDetail } from './blog-ui';

// CSS
import './app.css';

const App = () => {
    return (
        <HashRouter>
            <Routes>
                <Route element={<AdminSpace />}>
                    <Route path='admin-space' element={<CreatePost />}/>
                    <Route path='admin-space/list' element={<PostList />}/>
                </Route>
                <Route path='login' element={<Login />} />
                <Route path='detail/:uuid' element={<BlogDetail />} />
                <Route path='/' element={<LandingPage />} />
            </Routes>
        </HashRouter>
    );
};

export default App;
